.btn-rounded {
  border-radius: 50px;
}
.btn-blue {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  font-weight: bold;
  background: $blue;
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $white;
    background: $text-gray;
    box-shadow: 0 0 1px 1px $blue;
  }
  &:focus {
    color: $white;
    background: $text-gray;
    box-shadow: 0 0 1px 1px $blue;
  }
}
