footer {
  background: $blue;
  padding-top: 1rem;
  padding-bottom: 1rem;
  .row > * {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .row > *:nth-child(1) {
    order: 3;
    @media (min-width: 768px) {
      order: 1;
      justify-content: flex-start;
    }
  }
  .row > *:nth-child(2) {
    order: 2;
    @media (min-width: 768px) {
      order: 2;
      justify-content: center;
    }
  }
  .row > *:nth-child(3) {
    order: 1;
    margin-bottom: 1rem;
    @media (min-width: 768px) {
      order: 3;
      justify-content: flex-end;
      margin-bottom: 0;
    }
  }
  p {
    color: $white;
    margin: 0;
    font-size: 0.8rem;
  }
  a {
    color: $white;
    text-decoration: none;
    transition: all 0.3s;
    &:hover {
      color: $text-gray;
    }
  }
  .redes {
    margin: 0;
    .fa-stack {
      font-size: 0.9rem;
      .icon-red {
        font-size: 0.8rem;
      }
    }
  }
}